.section-contact{
    display: flex;
    margin: 20px 20px 0 20px;
    padding: 20px;
    background-color: var(--color-bg-dark);
    height: 97dvh;
    border-radius: 20px 20px 0 0;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}
.form{
    height: 50%;
    width: 100%;
    display: flex;
    flex-direction : column;
    align-items :center;
}
.input-name{
    background-color: var(--color-bg-dark);
    display: flex;
    width: 250px;
    height: 40px;
    margin: 10px 0 0 0;
    border: none;
    border-bottom: 2px solid var(--color-bg);
    color: var(--color-text);
    padding: 10px;
}
.input-email{
    background-color: var(--color-bg-dark);
    display: flex;
    width: 250px;
    height: 40px;
    margin: 10px 0 0 0;
    border: none;
    border-bottom: 2px solid var(--color-bg);
    color: var(--color-text);
    padding: 10px;
}
.input-text{
    background-color: var(--color-bg-dark);
    display: flex;
    width: 250px;
    height: 40px;
    margin: 10px 0 0 0;
    border: none;
    border-bottom: 2px solid var(--color-bg);
    color: var(--color-text);
    padding: 10px;
}
.btn-form{
    width: 20%;
    height: 5%;
    margin: 20px;
}
label{
    display: flex;
    flex-direction: column;
    margin: 10px;
}