.section-home{
    position: relative;
    display: flex;
    margin: 0 20px 0px 20px;
    padding: 20px;
    background-color: var(--color-bg-dark);
    height: 99dvh;
    border-radius: 0 0 20px 20px;
    flex-direction: column-reverse;
    justify-content: center;
    top: -60px;
    z-index: -1;
}
.text-home{
    margin: 80px 0 0 0;
    display: flex;
    flex-direction : column;
}
.text-home h1 {
    width: 100%;
    font-size: 1.6rem;
}
.text-home p {
    width: 90%;
    margin: 10px 0 0 0;

}
.icons-home{
    display: flex;
    margin: 20px 0 0 0;
}
.img-home{
 display: flex;
 justify-content:center;
 margin: 0 0 1em 0;
}
.img-home img{
    width: 90%;
}