.section-projects{
    display: flex;
    margin: 20px 20px;
    padding: 20px;
    background-color: var(--color-bg-dark);
    height: 97dvh;
    border-radius: 20px;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;

}
.projects{
    display: flex;
    flex-wrap :wrap;
    justify-content : center ;
    overflow: scroll;
}
.header-projects{
    margin: 10px;
}
.card{
    display: flex;
    border: 1px solid #ccc;
    border-radius: 10px;
    padding: 10px;
    width: 200px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
    margin: 10px;
    flex-direction :column;
    align-items: center;
}
.card-img{
    width: 80%;
    border-radius: 10px;
}