.section-about{
    display: flex;
    margin:0 20px 20px 20px;
    padding: 20px;
    background-color: var(--color-bg-dark);
    height: 99dvh;
    border-radius: 20px;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}
.text-about{
    display: flex;
}
.skills{
    display: flex;
    background-color: var(--color-bg);
    height: 20%;
    flex-direction: column;
    justify-content:center;
    width: 70%;
}
.box-skill{
    display: flex;
    height: 40%;
    background-color: rgb(0, 0, 0);
    border-radius: 10px;
    border: 2px solid rgb(34, 33, 103);
    margin: 5px 0 5px 0;
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
    padding: 5px ;
}
.box-skill div {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 0 5px 0;
} 
.line-skill{
    display: flex;
    background-color: rgb(35, 16, 212);
    border-radius: 20px;
    width: 90%;
    height: 7px;
    margin: 1px 0 0 1em;
}
.box-skill-css span{
    display: flex;
    background-color: rgb(35, 16, 212);
    border-radius: 20px;
    width: 80%;
    height: 7px;
} 
.box-skill-js span{
    display: flex;
    background-color: rgb(35, 16, 212);
    border-radius: 20px;
    width: 65%;
    height: 7px;

} 
.box-skill-react span{
    display: flex;
    background-color: rgb(35, 16, 212);
    border-radius: 20px;
    width: 75%;
    height: 7px;
} 
.about_img{
    display: flex;
    justify-content:center;
   }
   .about_img img{
       width: 40%;
   }